export default {
	data() {
		return {
			title: 'choose/previewImage',
			sourceTypeIndex: 2,
			sourceType: ['album', 'camera'],
			sizeType: ['original '], //默认全部原图，如果大于2M再进行压缩
			count: 1,
			uploadPaths: [],
			iamgesDomain: this.imagesDomain
		};
	},
	props: {
		width: {
			type: Number,
			default: 690
		},
		Title: {
			type: String,
			default: ''
		},
		imageList: {
			type: Array,
			default: undefined
		},
		images: {
			type: String,
			default: ''
		},
		placeholder: {
			type: String,
			default: '请写下内容描述'
		},
		problemDescText: {
			type: String,
			default: ''
		},
		autoOpenLogin: {
			//在上传登录时遇到登录失效情况是否自动打开登录页面
			type: Boolean,
			default: false
		},
		uploadScene: {
			//上传场景 ：
			//RESERVATION_SCENCE：1，
			// ORDER_PROCESSING_SCENCE：2  
			// ORDER_FINISH_SCENCE：3  
			// EVALUATION_SCENCE：4 
			// DEFAULT_SECENCE：5
			type: Number,
			default: 5
		},
		compressImageSize: {
			//当图片大于该值时进行压缩，默认2MB  ，单位：MB
			type: Number,
			default: 2
		}
	},
	methods: {
		previewImage: function(e) {
			var current = e.target.dataset.src
			var temArray = []
			if (this.images == '') {
				for (var index in this.imageList) {
					temArray.push(this.imageList[index])
				}
			} else {
				temArray.push(this.images)
			}
			console.log(current)
			uni.previewImage({
				current: current,
				urls: temArray
			})

		}
	}
}
